.app__menuitem {
    display: flex;
    flex-direction: column;

    width: 100%;
    margin: 1rem 0;

    .app__menuitem-head {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .app__menuitem-name {
            flex: 1;
        }

        .app__menuitem-price {
            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
            
        }
    }
}



